play: flex;\n\talign-items: center;\n\n\tfilter: drop-shadow(0 5px 5px rgba(0, 0, 0, .1));\n\n\t& > input,\n\t& > label {\n\t\ttransition: all .2s ease-in;\n\t\t&:focus {\n\t\t\toutline: none;\n\t\t}\n\t}\n\t& > input[type='email'] {\n\t\tflex: 1 0 auto;\n\t\t/* flex grow to fit the parent width */\n\t\twidth: 0;\n\t\theight: 44px;\n\t\tpadding: 5px 35px 5px 20px;\n\t\tborder-width: 0;\n\t\tborder-radius: 22px 0 0 22px;\n\t\tbackground-color: #fff;\n\t\tfont-size: 18px;\n\t\toverflow: hidden;\n\t\twhite-space: nowrap;\n\t\ttext-overflow: ellipsis;\n\t}\n\t& > .btn-blue {\n\t\t//height: 44px;\n\t\tmin-height: 0;\n\t\tmargin: 0;\n\t\tmargin-left: -25px;\n\t\tpadding: 10px 20px;\n\t\tcursor: pointer;\n\t\topacity: 1;\n\t\tline-height: 18px;\n\t\tmin-width: auto;\n\t\tdisplay: flex;\n\t\toverflow: hidden;\n\t\twhite-space: nowrap;\n\t\ttext-overflow: ellipsis;\n\t\tmax-width: 400px;\n\n\t\t.chevron-right-icon,\n\t\t.arrow-right-icon,\n\t\t.loading-icon {\n\t\t\tmargin-left: 10px;\n\t\t\ttransition: transform 200ms ease-in-out;\n\t\t}\n\t\t&:hover .arrow-right-icon,\n\t\t&:hover .chevron-right-icon {\n\t\t\ttransform: translateX(10px);\n\t\t}\n\t\t.loading-icon {\n\t\t\tanimation: 1600ms spin cubic-bezier(0.55, 0.75, 0.65, 0.25) infinite;\n\t\t}\n\t}\n\t&.icon-loading,\n\t&.error {\n\t\t&::after {\n\t\t\tleft: calc(100% - 32px);\n\t\t\tbox-sizing: content-box;\n\t\t}\n\t}\n}\n\n.checkboxes {\n\tposition: relative;\n\tdisplay: flex;\n\talign-items: flex-start;\n\tflex-direction: column;\n\tmargin: 10px auto;\n\tuser-select: none;\n\tspan {\n\t\tposition: relative;\n\t}\n\tinput {\n\t\tposition: absolute;\n\t\ttop: 14px;\n\t\topacity: 0;\n\t\t&:checked + label {\n\t\t\t&, &::before {\n\t\t\t\tcontent: '';\n\t\t\t\topacity: .7;\n\t\t\t}\n\t\t}\n\t}\n\tlabel {\n\t\tdisplay: flex;\n\t\talign-items: center;\n\t\tjustify-content: center;\n\t\tpadding: 8px 0;\n\t\tcursor: pointer;\n\t\topacity: .5;\n\t\t&, a {\n\t\t\tcolor: #fff;\n\t\t}\n\t\t&::before {\n\t\t\tdisplay: block;\n\t\t\twidth: 15px; // align w/ email input\n\t\t\theight: 15px;\n\t\t\tmargin-right: 5px;\n\t\t\tcontent: ' ';\n\t\t\ttext-align: center;\n\t\t\tborder: 1px solid rgba(255, 255, 255, .7);\n\t\t\tborder-radius: 3px;\n\t\t\tfont-size: 14px;\n\t\t\tfont-weight: 600;\n\t\t\tline-height: 14px;\n\t\t}\n\t}\n\n\tlabel.tos {\n\t\tpadding-bottom: 0;\n\t\topacity: .7;\n\t}\n}\n\n@keyframes spin {\n\t0% {\n\t\ttransform: rotate(0deg);\n\t}\n\t100% {\n\t\ttransform: rotate(360deg);\n\t}\n}\n\n"],"sourceRoot":""}]);
